@import url("https://cdn-uicons.flaticon.com/2.6.0/uicons-bold-straight/css/uicons-bold-straight.css");
@import url("https://cdn-uicons.flaticon.com/2.6.0/uicons-regular-rounded/css/uicons-regular-rounded.css");
@import url("https://cdn-uicons.flaticon.com/2.6.0/uicons-solid-rounded/css/uicons-solid-rounded.css");
@import url("https://cdn-uicons.flaticon.com/2.6.0/uicons-solid-straight/css/uicons-solid-straight.css");
@import url("https://cdn-uicons.flaticon.com/2.6.0/uicons-bold-rounded/css/uicons-bold-rounded.css");
@import url("https://cdn-uicons.flaticon.com/2.6.0/uicons-regular-straight/css/uicons-regular-straight.css");
@import url("https://cdn-uicons.flaticon.com/2.6.0/uicons-solid-rounded/css/uicons-solid-rounded.css");

// Font
@import url("https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Kumbh+Sans:wght@100..900&display=swap");

body {
  margin: 0;
  position: relative;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  max-width: 100vw;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Kanit", sans-serif;
  transition: 0.2s all ease-in-out;
  user-select: none;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}

// Loader
div {
  @keyframes loader {
    to {
      transform: rotate(0.5turn);
    }
  }

  &#loading {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100dvh;

    background: #cae5f1;
    color: #081c6d;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    span {
      width: 50px;
      aspect-ratio: 1;
      --_c: no-repeat radial-gradient(farthest-side, #25b09b 92%, #0000);
      background: var(--_c) top, var(--_c) left, var(--_c) right,
        var(--_c) bottom;
      background-size: 12px 12px;
      animation: loader 0.8s infinite;
      margin-bottom: 20px;
    }
  }
}

header {
  background: rgb(226, 233, 235);
  padding: 1rem 2rem;
  text-decoration: none;
  border-bottom: 3px solid rgba(12, 54, 113, 0.577);
  display: flex;

  h1 {
    color: #3d0547;
    font-size: 2rem;
    margin-right: 2rem;
    font-weight: 900;
    font-family: cursive;
    cursor: default;
  }

  nav {
    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 100%;
    max-height: 100dvh;

    #menu {
      display: none;

      padding: 0rem;
      border-radius: 50px;
      cursor: pointer;
      aspect-ratio: 1;
      font-size: 1.3rem;
      border: 2px solid rgba(255, 0, 0, 0);

      &:hover {
        background: #bcfcffed;
        border: 2px solid rgb(0, 0, 0);
      }
    }

    // .profile {
    //   background: rgba(255, 0, 0, 0.405);

    //   &.active {
    //     background: blue;
    //   }
    // }

    ul {
      display: flex;
      list-style: none;
      gap: 1rem;

      li {
        cursor: pointer;

        a {
          font-weight: 700;
          font-size: 1.6rem;
          text-decoration: none;
          padding: 10px 20px;
          border-radius: 20px;

          &:hover {
            color: #3d0547;
            background: #959bf363;
          }

          &.active {
            color: #060f34;
            background: #25b09b4b;
          }
        }

        &.login {
          a {
            color: #6f3909;

            &:hover {
              color: #402004;
              background: #a8b6ee;
            }
          }
        }

        &.sign-up {
          a {
            color: #ffffff;
            background: #3a25b0;

            &:hover {
              color: #ebffff;
              background: #060f34;
            }
            &.active {
              color: #fff;
              background: #081c6d;
            }
          }
        }
      }
    }
  }
}

main {
  padding: 2rem;
  background: rgb(218, 235, 240);
  min-height: 80vh;

  .hero {
    padding: 5rem;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;

    h1 {
      font-size: 2rem;
      color: #333;
      margin-bottom: 0.5rem;
    }

    button {
      padding: 10px 20px;
      background-color: #161cc4;
      color: white;
      border: none;
      border-radius: 5px;
      cursor: pointer;
      margin-top: 1.5rem;
      font-size: 1.2rem;
    }

    img {
      width: 30%;
    }
  }

  .services {
    padding: 5rem 0rem 8rem;
    display: flex;
    justify-content: space-around;
    gap: 1.5rem;
    margin: 20px;

    .service {
      background-color: white;
      border-radius: 8px;
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
      width: 30%;
      padding: 2rem 1.3rem;
      text-align: center;

      h3 {
        color: #4caf50;
        margin-bottom: 1rem;
      }
    }
  }

  &#sign-up {
    h1 {
      text-align: center;
    }

    .role {
      width: 100%;
      display: flex;
      justify-content: center;
      gap: 2rem;
      margin-top: 2rem;
      margin-bottom: 1rem;

      span {
        width: 100%;
        max-width: 200px;
        border: 1px solid blueviolet;
        border-radius: 20px;
        display: flex;
        text-align: center;
        justify-content: center;
        position: relative;

        &:hover {
          background: #1d4ff630;
        }
        &.selected {
          background: #1d4ff630;

          label {
            color: #e27430;
          }
        }

        label {
          width: 100%;
          padding: 1rem 2rem;
          padding-bottom: 5rem;
          font-size: 1.8rem;
          font-weight: 700;
          color: rgb(19, 74, 74);

          cursor: pointer;

          &:hover {
            color: #e27430;
          }
        }

        input {
          position: absolute;
          bottom: 1rem;
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }

    button.next {
      font-size: 1.2rem;
      font-weight: 800;

      padding: 0.6rem 2rem;
      margin-top: 1rem;

      color: #fff;
      background: linear-gradient(rgb(100, 100, 233), rgb(114, 152, 233));
      border: none;
      border-radius: 20rem;

      cursor: pointer;

      position: relative;
      left: 50%;
      transform: translateX(-50%);

      &:hover {
        color: #fff;
        background: rgb(70, 70, 226);
      }

      &.disable {
        cursor: no-drop;
        background: rgb(114, 152, 233);
      }
    }
  }
}

form {
  min-height: 100dvh;
  padding: 2rem;
  // background: rgba(0, 17, 255, 0.154);

  position: relative;
  left: 50%;
  transform: translateX(-50%);
  max-width: fit-content;

  &.loading {
    cursor: wait !important;
  }

  h1 {
    text-align: center;
    color: #091780;
    span {
      color: #0076d6;
    }
  }

  label {
    padding: 1rem;
    font-weight: 700;
    letter-spacing: 1px;
    font-size: 1.2rem;
    color: #6081c0;
  }
  input {
    padding: 0.5rem 1rem;
    width: fit-content;
    margin-top: 20px;
    border-radius: 20px;
    border: 1px solid #081c6d;
    color: #510d42ca;
    font-weight: 800;
    letter-spacing: 1px;

    &.err {
      border: 3px solid red;
      background: rgba(255, 35, 35, 0.031);
    }

    &.submit {
      color: #fff;
      font-size: 1rem;
      font-weight: 500;
      letter-spacing: 0.1rem;
      background: rgb(125, 20, 246);

      &.disable {
        cursor: wait;
        background: rgba(117, 63, 179, 0.681);
      }
    }
  }
}

ul#all-doctors {
  padding: 2rem;
  list-style: none;

  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 1.2rem;

  text-align: center;

  li {
    padding: 1rem;
    border-bottom: 1px solid rgba(0, 0, 0, 0.299);
    background: rgba(0, 47, 255, 0.113);
    border-radius: 20px;

    img {
      width: 30%;
      min-width: 80px;
      max-width: 100px;
      border-radius: 50%;
      border: 2px solid #5500ff92;
    }

    h1 {
      color: #081c6d;
      margin-bottom: 10px;
    }
    h2 {
      padding-top: 10px;
      border-top: 1px solid rgba(83, 10, 92, 0.153);
      color: #18246c8b;
    }
    h3 {
      color: #833d0b69;
    }

    div {
      display: flex;
      justify-content: center;
      margin-top: 1rem;
      gap: 1rem;

      button {
        padding: 0.8rem 1.5rem;
        border-radius: 10px;
        border: none;
        color: #fff;
        background: rgba(149, 0, 255, 0.89);
        font-weight: 700;
        width: 100%;
        cursor: pointer;

        &.aptmnt {
          background: #3f33c1;
        }
        &:hover {
          background: #060f34;
        }
      }
    }
  }
}

// Doctor Profile Start
main.profile {
  padding: 3rem;
  display: grid;
  place-items: center;

  section {
    border: 1px solid rgb(26, 9, 117);
    background: linear-gradient(to bottom right, #1d50f61d, #1d50f664);
    border-radius: 20px;
    padding: 2rem;
    max-width: fit-content;
    display: flex;
    flex-direction: column;

    .main {
      display: flex;
      align-items: center;
      gap: 2rem;
      margin-bottom: 2rem;

      h1 {
        font-size: 2.3rem;
        color: transparent;
        background: linear-gradient(
          to right bottom,
          #ff5500,
          #ff2a00,
          rgb(251, 13, 132)
        );
        background-clip: text;
        -webkit-background-clip: text;
      }

      h2 {
        color: #09114b96;
      }

      img {
        border-radius: 50%;
        max-width: 150px;
        border: 3px solid black;
      }
    }

    h3 {
      color: #3f086681;

      span {
        color: #260b71db;
      }
    }

    button {
      padding: 0.8rem 1.5rem;
      border-radius: 10px;
      border: none;
      color: #fff;
      background: rgba(149, 0, 255, 0.89);
      font-weight: 700;
      width: 100%;
      cursor: pointer;

      &.aptmnt {
        background: #3f33c1;
      }
      &:hover {
        background: #060f34;
      }
    }
  }
}
// Doctor Profile End

// Bottom Navigation
nav.bottom-navigation {
  display: none;
}

// Appointment Start

a button.book {
  color: #fff;
  background: #060f34;

  padding: 0.8rem 1.5rem;
  border-radius: 20px;
  font-size: 1rem;
}

// View Appointments Start
section.view-appointment {
  --orange: rgb(255, 191, 0);
  --green: greenyellow;

  p {
    .orange {
      color: rgb(255, 191, 0);
      background: #464a4d94;
      padding: 0.2rem 1rem;
      border-radius: 20px;
    }
    .green {
      color: greenyellow;
      background: #5a606294;
      padding: 0.2rem 1rem;
      border-radius: 20px;
    }
    .red {
      color: orangered;
      background: #7f838794;
      padding: 0.2rem 1rem;
      border-radius: 20px;
    }
  }

  ul {
    display: flex;
    flex-wrap: wrap;
    padding: 2rem;
    gap: 1rem;
    list-style-type: none;

    li {
      padding: 1rem;
      border: 2px solid #0076d6;
      background: #d3d0ff60;
      border-radius: 20px;

      img {
        background: #fff;
        max-width: 120px;
        object-fit: cover;
        border: 3px solid #060f34;
      }

      p {
        font-size: 1.2rem;
        color: #060f34c2;
        font-weight: 700;

        b {
          color: #060f34;
          font-weight: 800;
        }
      }
    }
  }

  select {
    padding: 0.2rem 1rem;
    border-radius: 20px;
    font-weight: 900;
    border: 2px solid #060f34;

    &.pending {
      color: rgb(255, 191, 0);
      background: #464a4d94;
    }
    &.accepted {
      color: greenyellow;
      background: #5a606294;
    }
    &.rejected {
      color: orangered;
      background: #7f838794;
    }
  }
}
// View Appointments End

// Appointment End

//

// Dashboard Start
button.logout {
  color: #fff;
  background: #f2155b;
  padding: 0.5rem 1.9rem;
  font-size: 1.3rem;
  font-weight: 800;
  border-radius: 2rem;
  cursor: pointer;
  display: block;
}
main#dashboard {
  min-height: fit-content;
  overflow-x: hidden;

  img {
    width: 200px;
    border-radius: 50%;
  }
  h1 {
    margin-bottom: 1rem;

    span {
      color: red;
    }
  }
  h2 {
    color: #0076d6;

    span {
      color: #091780;
    }
  }

  #edit {
    color: #ffff;
    background: #060f34;
    padding: 0.6rem 2rem;
    border-radius: 1rem;
    font-size: 1.3rem;
    font-weight: 700;
    cursor: pointer;
  }
}

// Booktime Start
section.date {
  form {
    min-width: 100%;

    .booktime {
      padding: 1rem 2rem;
      border-radius: 20px;
      font-size: 1.3rem;
    }
    textarea {
      padding: 1rem 2rem;
      width: 100%;
      border-radius: 20px;
      min-height: 18rem;
      box-sizing: border-box;
    }

    input[type="submit"] {
      color: #fff;
      padding: 0.8rem 1.3rem;
      border-radius: 20px;
      font-size: 1rem;
      font-weight: 500;
      letter-spacing: 0.1rem;
      background: rgb(125, 20, 246);
    }
  }
}
// Booktime End

// Dashboard End

