@media screen and (width > 1500px) {
  ul#all-doctors {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

@media screen and (width < 1000px) {
  main {
    margin-top: 64px;
    max-height: calc(100dvh - 64px);
    overflow: auto;
  }

  #home {
    flex-direction: column-reverse;
    gap: 1rem;

    img {
      width: 50vw;
    }

    h1 {
      width: 80vw;
      font-size: 2rem;
      font-weight: 600;

      color: #390ea4;
    }

    p {
      font-size: 1.2rem;
    }
  }

  ul#all-doctors {
    grid-template-columns: 1fr 1fr;
  }

  nav.mobile {
    position: fixed;
    top: 0;
    right: 0;
    max-width: 50vw;
    padding: 2rem;
    background: rgba(68, 32, 115, 0.223);
    backdrop-filter: blur(10px);
    justify-content: start;
    align-items: start;
    flex-direction: column-reverse;
    min-height: 100dvh;
    gap: 4rem;
    z-index: 999;

    &.close {
      transform: translateX(100%);

      #menu {
        position: absolute;
        top: 1rem;
        left: -100px;
      }
    }

    #menu {
      display: flex !important;
      align-items: center;
      justify-content: center;
      padding-top: 0.2rem;
      padding-left: 0.1rem;
      font-size: 1.2rem;
      text-align: center;
      width: 40px;
      height: 40px;
      background: #ffffff00;
    }

    ul {
      display: flex;
      flex-direction: column;
      gap: 2rem;

      li {
        a {
          font-weight: 600;
          font-size: 1.5rem;

          &:hover {
            color: #3d0547;
            background: #eff1ff63;
          }

          &.active {
            color: #060f34;
            background: #9efff194;
          }
        }

        &.login {
          a {
            color: #6f3909;
            background: #a3b3f37b;

            &:hover {
              color: #402004;
              background: #a8b6ee;
            }

            &.active {
              background: #bbc5eed4;
            }
          }
        }

        &.sign-up {
          a {
            color: #ffffff;
            background: #3a25b0;

            &:hover {
              color: #ebffff;
              background: #060f34;
            }
            &.active {
              color: #fff;
              background: #081c6d;
            }
          }
        }
      }
    }
  }

  nav.mobile {
    position: fixed;
    top: 0;
    right: 0;
    max-width: 50vw;
    padding: 2rem;
    background: rgba(68, 32, 115, 0.223);
    backdrop-filter: blur(10px);
    justify-content: start;
    align-items: start;
    flex-direction: column-reverse;
    min-height: 100dvh;
    gap: 4rem;
    z-index: 999;

    &.close {
      transform: translateX(100%);

      #menu {
        position: absolute;
        top: 1rem;
        left: -100px;
      }
    }

    #menu {
      display: flex !important;
      align-items: center;
      justify-content: center;
      padding-top: 0.2rem;
      padding-left: 0.1rem;
      font-size: 1.2rem;
      text-align: center;
      width: 40px;
      height: 40px;
      background: #ffffff00;
    }
  }

  // Bottom Navigation Start ==========
  #doctor-sidebar {
    display: none;
  }
  nav.bottom-navigation {
    display: block;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100vw;
    background: #ffffff;
    z-index: 10;
    box-shadow: 0 0px 20px rgba(0, 0, 0, 0.459);

    ul {
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      width: 100%;
      padding-block: 0.5rem;

      li {
        list-style-type: none;
        font-size: 1.5rem;

        a {
          display: block;
          position: relative;
          border-radius: 50%;
          width: 2em;
          aspect-ratio: 1;
          color: #455a64;
          transition: color 0s, background 0.6s !important;

          i {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }

          &:hover {
            background: #0a257631;
          }

          &.active {
            color: #fff;
            background: linear-gradient(#ef7850 2%, #ef5350, #dd1fe7dd);
            box-shadow: 0px 0px 12px rgba(23, 43, 97, 0.396);
          }
        }

        &#main-btn {
          a {
            color: #fff;
            background: linear-gradient(#02bcff, #0270ff);
            transform: translateY(-15px) scale(1.3);
            box-shadow: 0px 4px 10px rgba(50, 75, 143, 0.364);

            &.active {
              color: #fff;
              background: linear-gradient(#ef7850 2%, #ef5350, #dd1fe7dd);
              // background: linear-gradient(#ef5350, #8e24aa);
              box-shadow: 0px 4px 10px rgba(31, 54, 116, 0.599);
            }
          }
        }
      }
    }
  }

  // Bottom Navigation End  =========

  // Notification Icon Start ===
  header {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #fff;
    width: 100vw;
    padding: 10px 2rem;
    box-shadow: 0 0px 5px rgba(0, 0, 0, 0.613);
    border: none;
    z-index: 99;

    a {
      text-decoration: none;
    }

    a.icon {
      color: #455a64;
      width: 2em;
      border-radius: 50%;
      aspect-ratio: 1;
      transition: color 0s, background 0.6s !important;
      font-size: 1.5rem;
      padding: 0;
      position: relative;
      text-decoration: none;

      #notification {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -40%);
      }

      &:hover {
        background: #0a257631;
      }

      &.active {
        color: #fff;
        background: linear-gradient(#ef7850 2%, #ef5350, #dd1fe7dd);
        box-shadow: 0px 0px 12px rgba(23, 43, 97, 0.396);
      }
    }
  }
  // Notification Icon End ===

  // Appointment Start
  main:has(section.view-appointment) {
    padding: 0;
  }

  .appointment-header {
    padding: 1rem;
  }

  section.view-appointment {
    padding: 0;

    h2 {
      margin: 1rem;
    }

    ul {
      width: 100%;
      flex-direction: column;
      padding: 1rem;
      box-sizing: border-box;

      li {
        max-width: 100%;
        display: flex;
        flex-direction: column;

        p:last-child {
          margin-top: 0.5rem;
        }
      }
    }
  }
  // Appointment End

  #flex {
    max-width: 100vw !important;
  }
  main#dashboard {
    overflow: auto;
    max-width: 100vw !important;
    max-height: calc(100dvh - 130px) !important;
    margin-top: 68px;
    margin-bottom: 62px;

    &::-webkit-scrollbar {
      width: 11px;
    }
    &::-webkit-scrollbar-thumb {
      background: #920a67b4;
      border-radius: 20px;
      transition: 0.2s;

      &:hover {
        background: #d138b0;
      }
    }
  }
}

@media screen and (width < 780px) {
  .role {
    flex-wrap: wrap;
  }

  ul#all-doctors {
    grid-template-columns: 1fr;
  }
  .services {
    align-items: center;
    flex-direction: column;
    .service {
      min-width: 100%;
    }
  }
}

footer {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 10px 0;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
}

@media screen and (width > 1000px) {
  ul#all-doctors {
    grid-template-columns: 1fr 1fr;
  }

  #flex {
    display: flex;
    justify-content: end;

    #dashboard {
      width: 75%;
      max-height: 100dvh;
    }

    // Dashboard Sidebar Start
    header {
      position: relative;
      top: 0;
      left: 0;
      right: auto;
      z-index: 999;

      width: 100%;
      max-width: 25%;
      height: 100vh;
      max-height: 100dvh;
      padding: 2rem;
      overflow: auto;

      background: rgba(68, 32, 115, 0.223);
      backdrop-filter: blur(10px);

      display: flex;
      flex-direction: column;
      gap: 1rem;

      a.icon {
        display: none;
      }

      nav {
        width: 100%;
        display: flex;
        justify-content: start;
        align-items: start;
        flex-direction: column-reverse;
        gap: 4rem;

        #menu {
          display: none;
        }

        ul {
          display: flex;
          flex-direction: column;
          gap: 2rem;

          li {
            a {
              font-weight: 600;
              font-size: 1.5rem;

              &:hover {
                color: #3d0547;
                background: #eff1ff63;
              }

              &.active {
                color: #060f34;
                background: #9efff194;
              }
            }
          }
        }
      }
    }
  }
  // Dashboard Sidebar End
}
